<template>
  <div class="ambassador-container">
    <div class="header">
      <div class="card-wrapper">
        <div class="one">
          <div class="user">
            <van-image round width="50" height="50" src="https://img.yzcdn.cn/vant/cat.jpeg" />
            <div class="info">
              <div>
                <span>刘美丽</span>
                <van-icon class-prefix="iconfont icondengji" name="icondengji" />
                <span class="level">1</span>
              </div>
              <div>
                <span>官方认证推广大使</span>
              </div>
            </div>
          </div>
          <div class="commission_button">
            <span @click="path('ambassadorBrokerageInfo')">明细</span>
            <span class="button" @click="path('ambassadorBrokerageExtract')">佣金提取</span>
          </div>
        </div>
        <div class="two">
          <div>
            <span>当前可提取佣金</span>
          </div>
          <div>
            <span>¥</span>
            <span class="sum">888</span>
          </div>
        </div>
      </div>
      <div class="preferential-wrapper" @click="show=true">
        <div class="title-wrapper">
          <span>推广大使</span>
        </div>
        <div class="right-wrapper">
          <div class="line"></div>
          <div class="item">
            <div>
              <div class="title">点击生成优惠码</div>
              <div class="desc">已生成18个优惠码</div>
            </div>
            <div>
              <van-icon class-prefix="iconfont iconyou" name="iconyou" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-tabs
      v-model="active"
      title-active-color="#7c322f"
      color="#7c322f"
      line-width="70"
      line-height="2"
      title-inactive-color="#666666"
      swipeable
      class="tabs"
    >
      <van-tab title="有效优惠吗">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="cell" v-for="item in list" :key="item">
            <div class="right">
              <van-image
                round
                width="50"
                height="50"
                src="../../../static/image/ambassador_icon.png"
              />
              <div class="content">
                <div class="title">
                  <span class="bold" style="margin-right:5px;">9折优惠码：AK728</span>
                  <span>有效期15天</span>
                </div>
                <div class="label">
                  <span>
                    未支付订单12个,已确认订单20个
                    <span @click="path('ambassadorDiscountsInfo')">
                      详情
                      <van-icon class-prefix="iconfont iconyou" name="iconyou" />
                    </span>
                  </span>
                </div>
              </div>
            </div>
            <div class="left">
              <span class="sum">
                <span>¥</span>
                <span class="bold">56</span>
              </span>
              <span class="button" @click.stop="cancel">取消优惠</span>
            </div>
          </div>
        </van-list>
      </van-tab>
      <van-tab title="失效优惠码">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="cell" v-for="item in list" :key="item">
            <div class="right">
              <van-image
                round
                width="50"
                height="50"
                src="../../../static/image/ambassador_icons.png"
              />
              <div class="content">
                <div class="title">
                  <span
                    class="bold"
                    style="margin-right:5px;"
                    @click="path('ambassadorBrokerageInfo')"
                  >9折优惠码：AK728</span>
                  <span>有效期15天</span>
                </div>
                <div class="label">
                  <span>
                    未支付订单12个,已确认订单20个
                    <span @click="path('ambassadorDiscountsInfo')">
                      详情
                      <van-icon class-prefix="iconfont iconyou" name="iconyou" />
                    </span>
                  </span>
                </div>
              </div>
            </div>
            <div class="left">
              <span class="sum">
                <span>¥</span>
                <span class="bold">56</span>
              </span>
              <span class="failure">已失效</span>
            </div>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
    <van-popup v-model="show" round >
      <div class="wrapper">
        <div class="block" :style="{height:'100%'}" @click.stop>
          <div class="title-wrapper">
            选择折扣
            <i @click="show=false" class="iconfont iconcha right-icon"></i>
          </div>
          <div class="content-wrapper">
            <van-radio-group v-model="radio">
              <van-cell-group>
                <van-cell title="9折" clickable @click="radio = '1'">
                  <template #right-icon>
                    <van-radio name="1" />
                  </template>
                </van-cell>
                <van-cell title="8折" clickable @click="radio = '2'">
                  <template #right-icon>
                    <van-radio name="2" />
                  </template>
                </van-cell>
                <van-cell title="7折" clickable @click="radio = '3'">
                  <template #right-icon>
                    <van-radio name="3" />
                  </template>
                </van-cell>
                <van-cell title="6折" clickable @click="radio = '4'">
                  <template #right-icon>
                    <van-radio name="4" />
                  </template>
                </van-cell>
              </van-cell-group>
            </van-radio-group>
          </div>
          <div class="footer-wrapper">
              <div @click="show=false">取消</div>
              <div>确认</div>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      radio:1,
      show: false,
      active: 0,
      list: [],
      loading: false,
      finished: false,
    };
  },
  watch: {},
  computed: {},
  methods: {
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 5; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
    cancel() {
      console.log(1);
    },
    path(name) {
      this.$router.push({ name });
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.ambassador-container {
  width: 100%;
  min-height: 100vh;
  padding: 15px;
  background-color: #fff;
  .header {
    .card-wrapper {
      width: 100%;
      height: 160px;
      background: linear-gradient(90deg, #e6c46a, #c8a060);
      border-radius: 10px;
      font-size: 15px;
      color: #fff;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      margin-bottom: 15px;
      .one,
      .two {
        display: flex;
      }
      .user {
        display: flex;
        align-items: center;
        flex: 1;
        padding: 15px 0 0 15px;
        .info {
          margin-left: 8px;
          > div {
            margin-bottom: 4px;
            &:last-of-type {
              margin-bottom: 0;
            }
            i {
              margin-left: 0px;
              font-size: 13px;
            }
            .level {
              position: relative;
              top: -0.5px;
              right: 19px;
              font-size: 12px;
              color: #c8a060;
            }
          }
          > div:last-of-type {
            font-size: 10px;
            font-weight: 400;
          }
        }
      }
      .commission_button {
        display: flex;
        align-items: center;
        padding: 15px 15px 0 0;
        font-size: 10px;
        .button {
          margin-left: 5px;
          background-color: #fff;
          border: solid 1px #fff;
          color: #b18743;
          padding: 4px 8px;
          font-size: 12px;
          font-weight: 500;
          border-radius: 25px;
        }
      }
      .two {
        flex: 1;
        padding: 0 15px 15px;
        font-size: 0;
        justify-content: space-between;
        align-items: flex-end;
        span {
          font-size: 15px;
        }
        .sum {
          font-size: 24px;
        }
      }
    }
    .preferential-wrapper {
      display: flex;
      background: #ffffff;
      box-shadow: 0px 0px 18px 0px rgba(153, 153, 153, 0.3);
      border-radius: 10px;
      padding: 15px;
      .title-wrapper {
        display: flex;
        align-items: center;
        span {
          display: inline-block;
          width: 35px;
          font-weight: bold;
          background: linear-gradient(0deg, #c8a060 0%, #e6c46a 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
      .right-wrapper {
        flex: 1;
        display: flex;
        align-items: center;
        .line {
          width: 1px;
          height: 80%;
          margin: 0 13px 0 9px;
          background: #cccccc;
        }
        .item {
          height: 100%;
          flex: 1;
          display: flex;
          align-items: center;
          > div:first-child {
            flex: 1;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .title {
              color: #333333;
            }
            .desc {
              color: #666666;
              font-size: 10px;
            }
          }
          > div:last-of-type {
            width: 22px;
            i {
              font-size: 22px;
              color: #999999;
            }
          }
        }
      }
    }
  }
  /deep/ .tabs {
    margin-top: 20px;
    width: 100%;
    height: auto;
    pointer-events: none;
    .van-tabs__wrap {
      pointer-events: all;
      height: 40px;
    }
    .van-tabs__wrap::after {
      border-width: 0;
    }
    .van-tabs__content {
      pointer-events: all;
    }
  }
  .cell {
    position: relative;
    display: flex;
    padding: 15px 0;
    border-bottom: 1px solid #dbdbdb;
    .bold {
      color: #333333;
      font-size: 16px;
    }
    .right {
      flex: 1;
      display: flex;
      align-items: center;
      font-size: 10px;
      .content {
        padding: 2px 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 8px;
        .title {
          color: #999999;
        }
        .label {
          display: flex;
          align-items: baseline;
          color: #666666;
          i {
            font-size: 10px;
          }
        }
      }
    }
    .left {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      font-size: 10px;
      .sum {
        font-size: 0;
        > span {
          font-size: 10px;
        }
        .bold {
          color: #333333;
          font-size: 16px;
        }
      }
      .button {
        padding: 4px 5px;
        color: #fff;
        background: linear-gradient(90deg, #c8a060, #e6c46a);
        border-radius: 5px;
      }
      .failure {
        padding: 4px 5px;
        color: #fff;
        background: #bfbfbf;
        border-radius: 5px;
      }
    }
  }
}
</style>


